import React, { Component } from 'react';
import { Dropdown } from 'react-bootstrap';
import { ContextMenu, MenuItem, ContextMenuTrigger, SubMenu } from "react-contextmenu";
import PerfectScrollbar from 'react-perfect-scrollbar';
import { GET_MEDIA_NAME } from '../../../../../Services/Utils/utils';
class BulkCopy extends Component {

    render() {
        const { collections,onSelectToCopy } = this.props
        return  <Dropdown align="start" className="block-center position-relative">
                    <Dropdown.Toggle variant="link" className="btn text-decoration-none p-0 d-inline-flex align-items-center me-2 arrow_disabled">
                        <i className="ri-file-copy-line icon-6 me-1"></i>
                        <span className="text-gray-800 fs-7">Copy</span>
                    </Dropdown.Toggle>

                    {collections && collections.length > 0 ?
                        <Dropdown.Menu style={{ width: 230 }}>
                            <PerfectScrollbar options={{ wheelPropagation: false }}>
                                <div style={{ minHeight: 180, maxHeight: 380 }}>
                                    {collections && collections.length > 0 ?
                                    collections.map((cItem,copyIndex) =>
                                        <div key={`b_copy${copyIndex}`} className="menu-item" onClick={onSelectToCopy(cItem.id)}>
                                            <Dropdown.Item>
                                                <div className="symbol symbol-24 flex-shrink-0 me-2 bg-light" style={{borderRadius:2}}>
                                                    {cItem.thumbnails ? <img className="img-fluid object-center-cover" src={cItem.thumbnails} height={54} width={54} alt={GET_MEDIA_NAME(cItem.thumbnails)} /> :  <i className="ri-folder-open-line me-0"></i>}
                                                </div>
                                                <span className="menu-title text-break">{cItem.name}</span>
                                            </Dropdown.Item>
                                        </div>
                                    ) : null}
                                </div>
                            </PerfectScrollbar>
                        </Dropdown.Menu>
                        : null}
                </Dropdown>
        
        
    }
}
export default BulkCopy
